import Vue from 'vue'
import Router from 'vue-router'
import Record from '@/components/page/Record'
import Register from '@/components/page/Register'
import Charge from '@/components/page/Charge'
import CentralCharge from '@/components/page/CentralCharge'
import Search from '@/components/page/Search'
import Login from '@/components/Login'
import IndexMain from '@/components/IndexMain'
import selectTer from '@/components/selectTer'
import User from '@/components/user'
import ManualEntry from '@/components/page/ManualEntry'
import ManualExit from '@/components/page/ManualExit'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Login
    },
    {
      path: '/selectTer',
      component: selectTer
    },
    {
      path: '/user',
      component: User
    },
    {
      path: '/index',
      component: IndexMain,
      children: [
        {
          path: '/',
          component: Record
        },
        {
          path: '/record',
          component: Record
        },
        {
          path: '/register',
          component: Register
        },
        {
          path: '/charge',
          component: Charge
        },
        {
          path: '/centralcharge',
          component: CentralCharge
        },
        {
          path: '/manualEntry',
          component: ManualEntry
        },
        {
          path: '/Manualexit',
          component: ManualExit
        },
        {
          path: '/search',
          component: Search
        }
      ]
    }
  ]
})



