<template>
  <div>
    <header data-am-widget="header" class="am-header am-header-default nav-mb-bar">
      <div class="am-header-left am-header-nav" @click="esc">
        <a href="#/index" class="">
          <i class="am-header-icon am-icon-chevron-left"></i>
        </a>
      </div>
      <h1 class="am-header-title">移动收费终端</h1>
    </header>
    <div class="login">

      <div  class="userlogin">
        <ul>
          <li>
            <router-link :to="{ path: '/user', query: { username: '用户3' }}" >
              <div class="imgbox">
                <i class="am-header-icon am-icon-user"></i>
              </div>
              <p>{{this.$route.query.username }} </p>
            </router-link>
          </li>
        </ul>
      </div>
        
        <el-form ref="form" :model="form" label-width="80px">
          
          <el-input v-model="form.password" class="useinput radius_50 password" placeholder="密码"></el-input>
          
          <el-button type="primary" @click="login">登录</el-button>
            
        </el-form>

    </div>
</div>
</template>

<script>

export default {
  name: 'Login',
  data () {
    return {
      form: {
        password: ''
      }
    }
  },
  methods: {
    login () {
      this.$router.push('/selectTer')
    },
    esc () {
      this.$router.push('/')
    }
  }
}
</script>

<style scoped>
  .login{
    width:80%;
    margin: 0px auto;
    padding-top:120px;
    text-align: center;
    position: relative;
    max-width: 320px;
  }
  .useinput{
    margin-bottom: 25px;
  }
 .el-button{
    width: 100%;
    border-radius: 50px;
  }
.useinput{
    margin-bottom: 15px;
  }
  .switch{
    position: fixed;
    left: 18px;
    bottom: 3px;
    font-size: 14px;
    color: #888;
  }
  ul{
    padding: 0px;
    margin: 0 auto;
  }
  ul li{
    list-style: none;
    width: 70px;
    margin: 5px;
    display: inline-block;
  }
  ul li .imgbox{
    width: 60px;
    height: 60px;
    margin: 0 auto;
    background: #999;
    border-radius: 50%;
    border: 4px solid #accff7;
    background: #3d96fa;
    color: #fff;
    overflow: hidden;
  }
  ul li .imgbox i{
    font-size: 60px;
    line-height: 0px;
  }
  ul li p{
    margin: 5px;
    font-size: 14px;
    color: #666;
    display: inline-block;
  }
</style>



