<template>

<!--pc端-->
<div>
<header class="am-topbar am-topbar am-topbar-inverse nav-pc">
  <el-menu theme="dark" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
    <div class="user_box">
      <div class="user_img">
        <i class="am-header-icon am-icon-user"></i>
        <!--<img src="../assets/img/666.jpg" alt="">-->
      </div>
      <h3>值班员</h3>
    </div>
    <el-submenu index="1">
      <template slot="title">车辆记录</template>
      <el-menu-item index="1-1">选项1</el-menu-item>
      <el-menu-item index="1-2">选项2</el-menu-item>
      <el-menu-item index="1-3">选项3</el-menu-item>
    </el-submenu>
    <el-submenu index="2">
      <template slot="title">信息查询</template>
      <el-menu-item index="2-1">选项1</el-menu-item>
      <el-menu-item index="2-2">选项2</el-menu-item>
      <el-menu-item index="2-3">选项3</el-menu-item>
    </el-submenu>
    <el-menu-item index="3">
      <router-link to="/">交班/下班</router-link>
    </el-menu-item>
    <el-menu-item index="4">
      <router-link to="/">免费放行原因</router-link>
    </el-menu-item>
    <el-menu-item index="5">
      <router-link to="/">到访原因</router-link>
    </el-menu-item>
    <el-menu-item index="6">
      <router-link to="/">修改车位</router-link>
    </el-menu-item>
    <el-submenu index="7" class="last_right">
      <template slot="title">用户1</template>
      <el-menu-item index="2-1">退出登录</el-menu-item>
      <el-menu-item index="2-2">切换用户</el-menu-item>
    </el-submenu>
  </el-menu>
</header>


<!--移动端-->

  <header data-am-widget="header" class="am-header am-header-default nav-mb-bar">
    <div class="am-header-left am-header-nav" v-if="bar_show" @click="back">
      
        <i class="am-header-icon am-icon-chevron-left"></i>
      
    </div>
    <h1 class="am-header-title">{{headTitle}}</h1>

  </header>


  <nav data-am-widget="menu" class="am-menu  am-menu-offcanvas1 nav-mb" data-am-menu-offcanvas>
    <a href="javascript: void(0)" class="am-menu-toggle" @click.stop="toggle_menu">
      <i class="am-menu-toggle-icon am-icon-bars"></i>
    </a>


  <div >
    <div class="am-offcanvas am-active" @click.stop="toggle_menu" v-if="isActive"></div>
      <div class="am-offcanvas-bar am-offcanvas-bar-active" v-bind:class="{'am-active6':isActive}">

        <div class="userPic clear">
          <div class="userimg">
            <img src="../assets/img/666.jpg" alt="">
          </div>
          <div class="userText">
            <span class="username">收费员1</span>
            <span class="number">138-8888-8888</span>
          </div>
        </div>

        <ul class="am-menu-nav am-avg-sm-1">
          <li class="am-parent">
            <a href="javascript: void(0)" class="" @click.stop="show_more = !show_more; if(show_more){show_more2 = !show_more}" >车辆记录</a>
            <ul class="am-menu-sub am-collapse  am-avg-sm-2" v-bind:class="{'am-in':show_more}">
              <li class="">
                <a href="javascript: void(0)" class="">公司</a>
              </li>
              <li class="">
                <a href="javascript: void(0)" class="">人物</a>
              </li>
            </ul>
          </li>
         <li class="am-parent">
            <a href="javascript: void(0)" class="" @click.stop="show_more2 = !show_more2;if(show_more2){show_more = !show_more2}" >信息查询</a>
            <ul class="am-menu-sub am-collapse  am-avg-sm-2" v-bind:class="{'am-in':show_more2}">
              <li class="">
                <a href="javascript: void(0)" class="">公司</a>
              </li>
              <li class="">
                <a href="javascript: void(0)" class="">人物</a>
              </li>
            </ul>
          </li>
          <li ><router-link to="/">交班/下班</router-link></li>
          <li ><router-link to="/">免费放行原因</router-link></li>
          <li ><router-link to="/">到访原因</router-link></li>
          <li ><router-link to="/">修改车位</router-link></li>
          <li ><router-link to="/">退出系统</router-link></li>
         
        </ul>

      </div>
    </div>

  </nav>

  <!--移动端-->


  </div>
</template>

<script>

export default {
  name: 'Naver',
  data () {
    return {
      headTitle: '南门收费',
      activeIndex: '1',
      activeIndex2: '1',
      bar_show: 0,
      isActive: 0,
      show_more: 0,
      show_more2: 0
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
    },
    toggle_menu () {
      this.isActive = !this.isActive
    },
    show_more () {
      console.log('888')
    },
    back () {
      this.$router.go(-1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
@media only screen and (min-width:741px) {
  .nav-mb{
      display: none;
  }
  .nav-mb-bar{
      display: none;
  }
}

@media only screen and (max-width: 740px) {
  .nav-pc{
      display: none;
  }
}
.nav-pc{
  margin-bottom: 0px;
}
.am-offcanvas-bar-active{
  transition: all 0.3s ease;
}
.am-collapse{
  transition: all 0.3s ease;
}
.am-active6{
  left: 270px !important;
}

.user_menu{
  float: left;
  line-height: 50px;
  font-size: 18px;
  font-weight: 400;
  padding: 0 20px;
  margin: 0px;
}
.el-menu-item.is-active{
  color: #fff;
}
.last_right{
  float: right !important;
}
</style>




