<template>
  <div class="CentralCharge">
    <div class="search">
      <span>查询：</span>
      <el-input placeholder="请输入内容" icon="search" :on-icon-click="handleIconClick" v-model="input5">
        <el-select v-model="select" slot="prepend" placeholder="粤">
          <el-option label="粤" value="1"></el-option>
        </el-select>
      </el-input>
    </div>
    <DataTable></DataTable>
  </div>
</template>
<script>
import DataTable from './DataTable'
export default {
  data () {
    return {
      activeName: 'first',
      input5: '',
      select: '粤'
    }
  },
  methods: {
    handleClick (tab, event) {
      console.log(tab, event)
    },
    handleIconClick (ev) {
      console.log(ev)
    }
  },
  created () {
    this.$parent.$children[0].headTitle = '信息查询'
    this.$parent.$children[0].bar_show = 1
  },
  components: {
    DataTable
  }
}

</script>

<style scoped>
.search{
  height: 65px;
  background: #fff;
  padding: 15px 10px;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.search > span{
  float: left;
  line-height: 35px;
}
.el-input-group{
  float: left;
  width: 70%;
  max-width: 400px;
}
.carList li {
  padding: 10px;
  background: #fff;
  border-bottom: 1px solid #ddd;
}
.carList li .imgbox{
  width: 60px;
  height: 60px;
  float: left;
}
.carList li .imgbox img{
  width: 100%;
  height: 100%;
}
.carList li .text{
  padding: 5px 0 0 10px;
  float: left;
}
.carList li .text p{
  margin: 0px;
  color: #000;
  font-size: 16px;
  font-weight: 500;
}
.carList li .text span{
  color: #999;
  font-size: 14px;
}

</style>



