<template>
    <div>
        <div class="list_ul">
            <dl>
              <dt>车牌号码</dt>
              <dd><input type="text" placeholder="车牌号码"/></dd>
            </dl>
            <dl>
              <dt>车牌颜色</dt>
              <dd>
                <select>
                  <option>蓝色</option>
                  <option>白色</option>
                  <option>黑色</option>
                </select>
              </dd>
            </dl>
            <dl>
              <dt>注册号码</dt>
              <dd><input type="text" placeholder="注册号码"/></dd>
            </dl>
            <dl>
              <dt>访客信息</dt>
              <dd>
                <textarea cols="30" rows="3"></textarea>   
              </dd>
            </dl>
        </div>

        <div class="fixed_but">
            <ul>
                <li>
                    <router-link to="/charge">确认</router-link>
                </li>
                <li class="yellow">取消</li>
            </ul>
        </div>
    </div>
</template>

<script type="text/javascript">
export default {
  created () {
    this.$parent.$children[0].headTitle = '手动入场'
    this.$parent.$children[0].bar_show = 1
  }
}
</script>

<style scoped>
  .list_ul{
    margin:3% auto;
  }
  .list_ul dl{
    width:80%;
    margin: 3% auto;
  }
  .list_ul dt,dd{
    margin:0px auto;
  }
  .list_ul dt,dd,input,select,textarea{
    width:100%;
  }
</style>



