<template>
  <div>
    <header data-am-widget="header" class="am-header am-header-default nav-mb-bar">
      <h1 class="am-header-title">移动收费终端</h1>
    </header>
    <div class="login">

      <div v-if="show">

        <h3 class="title">账号密码登陆</h3>

        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px">
          <el-form-item prop="username">
            <el-input v-model="ruleForm.username" placeholder="用户名" class="radius_50 username"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input type="password" placeholder="密码" v-model="ruleForm.password" @keyup.enter.native="login" class="radius_50 password"></el-input>
          </el-form-item>
          <div class="login-btn">
            <el-button type="primary" @click="login">登录</el-button>
          </div>
        </el-form>
      </div>

      <div v-if="!show" class="userlogin">

        <h3 class="title">用户快捷登陆</h3>

        <div class="user_but but_left" @click="userRight">
          <i class="am-header-icon am-icon-caret-left"></i>
        </div>
        <div class="user_but but_right"  @click="userLeft">
          <i class="am-header-icon am-icon-caret-right"></i>
        </div>
        <div class="list_box">
          <ul class="user_list" :style="{ left: list_left + 'px' }">

            <li v-for="n in 4">
              <router-link :to="{ path: '/user', query: { username: '用户'+n }}">
                <div class="imgbox">
                  <i class="am-header-icon am-icon-user"></i>
                </div>
                <p>收费员{{n}}</p>
              </router-link>
            </li>
            
          </ul>
        </div>
      </div>



    </div>
      <div class="switch">
        <p @click="handchange">{{ switchLogin }}</p>
      </div>
  </div>
</template>


<script>

export default {
  name: 'Login',
  data () {
    return {
      switchLogin: '切换到快速登陆',
      show: 0,
      ruleForm: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      },
      list_left: 0
    }
  },
  methods: {
    login () {
      this.$router.push('/selectTer')
    },
    handchange () {
      if (this.show) {
        this.switchLogin = '转到账号密码登陆'
      } else {
        this.switchLogin = '切换到快速登陆'
      }
      this.show = !this.show
    },
    userRight () {
      if (this.list_left < 0) {
        this.list_left = this.list_left + 80
      }
    },
    userLeft () {
      if (this.list_left > -80) {
        this.list_left = this.list_left - 80
      }
    }
  }
}
</script>

<style scoped>
@media only screen and (min-width:741px) {
  
}

@media only screen and (max-width: 740px) {
  .title{
    display: none;
  }
}
  .login{
    width:80%;
    margin: 0px auto;
    padding-top: 120px;
    text-align: center;
    position: relative;
    max-width: 320px;
  }
  
  h3{
    font-size: 22px;
  }
  .el-button{
    margin: 0px !important;
    border-radius: 50px;
    width: 100%;
  }
  .userlogin{
    position: relative;
  }
  .userlogin .user_but{
    position: absolute;
    bottom: 50px;
    font-size: 22px;
    color: #3d96fa;
  }
  .userlogin .but_left{
    left: 0px;
  }
  .userlogin .but_right {
    right: 0px;
  }
.list_box{
  height: 102px;
  overflow: hidden;
  width: 240px;
  margin: 0 auto;
  position: relative;
}
.user_list{
  position: absolute;
  width: 500%;
  transition: all 0.5s ease;
}
.useinput{
    margin-bottom: 15px;
  }
  .switch{
    position: absolute;
    left: 18px;
    bottom: 3px;
    font-size: 14px;
    color: #888;
  }
  ul{
    padding: 0px;
    margin: 0 auto;
  }
  ul li{
    list-style: none;
    width: 70px;
    margin: 5px;
    float: left;
  }
  ul li .imgbox{
    width: 60px;
    height: 60px;
    margin: 0 auto;
    background: #999;
    border-radius: 50%;
    border: 4px solid #accff7;
    background: #3d96fa;
    color: #fff;
    overflow: hidden;
  }
  ul li .imgbox i{
    font-size: 60px;
    line-height: 0px;
  }
  ul li p{
    margin: 5px;
    font-size: 14px;
    color: #666;
    display: inline-block;
  }
</style>



