<template>
    
<div class="am-g">

<div class="warning">
  <Warning></Warning>
</div>


<el-row class="am-u-sm-12 ">

  

  <p class="title"><i class="am-header-icon am-icon-arrow-circle-o-left"></i><span>南门入口通道一</span></p>
  <el-col>
    <el-card :body-style="{ padding: '0px' }">

      <div class="imgbox am-u-md-5  ">
        <p>开闸</p>
        <p>关闸</p>
        <img src="./../../assets/img/666.png" />
      </div>
      <div class="text am-u-md-7">
        <span>粤BW4Q53【零食车】</span>
        <div class="button">
          <span class="number">注册号：123455</span>
          <span class="number bigshow">入场时间：2017-2-22 12:00:00</span>          
        </div>
        <div class="but_list">
          <span class=" but" @click="register">登记</span>
          <span class=" but" @click="register">开闸</span>
          <span class=" but" @click="register">关闸</span>
        </div>
      </div>
    </el-card>
  </el-col>
</el-row>



      <el-row class="am-u-sm-12 ">
        <p class="title"><i class="am-header-icon am-icon-arrow-circle-o-right"></i><span>南门入口通道一</span></p>
        <el-col > 
          <el-card :body-style="{ padding: '0px' }">
            <div class="imgbox am-u-md-5  ">
              <p>开闸</p>
              <p>关闸</p>
              <img src="./../../assets/img/666.png" />
            </div>
            <div class="text am-u-md-7  ">
              <span>粤BW4Q53【零食车】</span>
              <div class="button">
                <span class="number">注册号：123455</span>
                <span class="number bigshow">停车时间：1小时02分钟26秒</span>
                <span class="number bigshow">车辆类型：小轿车</span>
                <span class="price">￥: 5.00</span>
              </div>
              <div class="but_list">
                <span class="but" @click="register">收费</span>
                <span class="but but2" @click="register">开闸</span>
                <span class="but but2" @click="register">关闸</span>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>

    </div>

</template>

<script>
import Warning from './warning'
export default {
  data () {
    return {
      msg: '车辆记录',
      screenWidth: document.body.clientWidth
    }
  },
  created () {
    this.$parent.$children[0].headTitle = '南门收费'
    this.$parent.$children[0].bar_show = 0
  },
  mounted () {
    const that = this
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        that.screenWidth = window.screenWidth
      })()
    }
  },
  methods: {
    register () {
      this.$router.push('/register')
    }
  },
  watch: {
    screenWidth (val) {
      this.screenWidth = val
      console.log(this.screenWidth)
    }
  },
  components: {
    Warning
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@media only screen and (max-width: 640px){
  .bigshow{
    display: none !important;
  }
  .imgbox{
    max-height: 160px;
  }
}
@media only screen and (min-width:641px) {
  .price{
    position: absolute;
    right:30px;
    top: 20px;
    font-size: 35px !important;
  }
  .imgbox{
    width: 40% !important;
    padding: 10px !important;
  }
  .imgbox p{
    display: none;
  }
  .text{
    position: static !important;
    padding: 40px 0px !important;
  }
  .text > span{
    font-size: 22px !important;
  }
  .but_list{
    position: absolute;
    right: 0px;
    bottom: 30px;
    width: 58%;
  }
  .but_list .but{
    float: left;
    margin: 0 5px 0 5px;
    position: static !important;
  }
  .text .button .number{
    font-size: 18px !important;
    margin: 8px 0!important;
  }
  .text .button .number{
    font-size: 20px;
  }
  p.title{
    font-size:20px;
  }
  .text .but_list .but{
    width: 25% !important;
    height: 50px;
    line-height: 45px;
    padding: 0px !important;
    font-size: 20px !important;
    margin-right: 5%;
  }
}
@media  (min-width: 641px) and (max-width:1000px) {
.text{
  padding: 10px 0px !important
}
.text .but_list .but{
    height: 35px !important;
    line-height: 35px;
    font-size: 16px !important;
  }
  .but_list{
    bottom: 15px;
  }
  .text .button .number{
    font-size: 16px !important;
  }
}
.el-col{
  position: relative;
}
.imgbox{
  width: 100%;
  background: #fff;
  position: relative;
  overflow: hidden;
  padding: 0px;
}
.imgbox img{
  width: 100%;
  height: 100%;
}
.imgbox p{
  position: absolute;
  top: 10px;
  width: 40px;
  height: 40px;
  border-radius: 40px;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  line-height: 40px;
  text-align: center;
  margin: 0px;
  font-size: 14px;
}
.imgbox p:nth-of-type(1){
  left: 10px;
}
.imgbox p:nth-of-type(2){
  right: 10px;
}
.text{
  padding:6px 15px 8px;
  position: relative;
}
.text span{
  color: #2196f5;
  font-size: 16px;
}
.text .button .number{
  color: #999;
  font-size: 14px;
  display:block;
  margin:5px 0 0; 
}
.text .button .price{
  font-size: 19px;
  color:#ff9813;
}
.text .but_list .but{
  color: #fff;
  background: #2196f3;
  padding: 5px 0px;
  border-radius: 100px;
  width: 100px;
  text-align: center;
  font-size: 14px;
  position: absolute;
  right: 15px;
  bottom: 11px;
}
.text .but_list .but2{
  bottom: 53px;
}
p.title{
  margin: 7px 8px;
}
p.title i{
  color: #2196f3;
  margin-right:5px; 
}
p.title span{
  color: #000;
}
</style>



