<template>
  <div class="clear" style="background: #fff;position:relative;">
    <div class="clear">
      <div>
        <div class="imgbox ">
          <img src="../../assets/img/666.jpg" alt="">
        </div>
      </div>
      <div class="list_ul am-u-md-6" >
        <ul>
          <li>
            <span class="text">车牌号码：</span>
            <span class="more">
              <div class="el-input">
                <input type="text" value="粤BW4Q53"  class="el-input__inner" />
              </div>
            </span>
          </li>
          <li>
            <span class="text">注册号码：</span>
            <span class="more">
              <div class="el-input">
                <input type="text" value="123456789"  class="el-input__inner" />
              </div>
            </span>
          </li>
          <li>
            <span class="text">证件号码：</span>
            <span class="more">
              <div class="el-input">
                <input type="text" value="49999999999"  class="el-input__inner" />
              </div>
            </span>
          </li>
          <transition name="fade">
            <div>
              <li>
                <span class="text">到访地址：</span>
                <span class="more">
                  <div class="el-input">
                    <input type="text" value="顶顶顶顶顶顶"  class="el-input__inner" />
                  </div>
                </span>
              </li>
            </div>
            </transition>
        </ul>
        </div>
  </div>
    <div class="bottom_text">
      <p>到访事由：</p>
      <ul class="clear">
        <li v-bind:class="{ active: cell === selected }" @click="choose(cell)" v-for="cell in list">{{cell}}</li>
      </ul>
      <p>其他事由：</p>
      <div class="textArea">
        <el-input type="textarea" v-model="desc"></el-input>
      </div>
    </div>


    <div class="fixed_but">
      <ul>
        <li>
          <router-link to="/charge">确认入场</router-link>
        </li>
        <li class="yellow">取消入场</li>
      </ul>
    </div>

  </div>
</template>

<script>
export default {
  data () {
    return {
      list: ['煤气检修', '送快递', '送家具、家电', '亲友', '家电维修1', '家电维修2', '家电维修3', '家电维'],
      select: '小型车',
      more: '展开更多',
      desc: '',
      selected: null
    }
  },
  methods: {
    choose (index) {
      this.selected = index
    }
  },
  created () {
    this.$parent.$children[0].headTitle = '登记入场'
    this.$parent.$children[0].bar_show = 1
  }
}

</script>

<style scoped>
  [class*=am-u-]{
    padding: 0px;
  }
  .imgbox {
    width: 100%;
    overflow: hidden;
  }
  
  .imgbox img {
    width: 100%;
  }
   @media only screen and (max-width:641px) {
     .bottom_text{
       display: none
     }
   }
  @media only screen and (min-width:641px) {
    .imgbox{
      width:50%;
      float: left;
      padding: 20px;
    }
    .imgbox img{
      border: 6px solid #fff;
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.5)
    }

  }
  .bottom_text{
    padding:20px 30px;
    color: #666;
    margin-bottom: 80px;
  }
  .bottom_text p{
    color: #999;
  }
  .bottom_text ul li.active{
    background: #2196f3;
    color: #fff;
    border: 1px solid #2196f3;
  }
  .bottom_text ul li{
    float: left;
    list-style: none;
    padding: 5px 20px;
    min-width: 100px;
    text-align: center;
    margin-right: 20px;
    margin-bottom: 15px;
    color: #666;
    background: #f5f5f5;
    border-radius: 4px;
    border: 1px solid #ddd; 
    cursor: pointer;

  }

</style>



