  <template>
    <div class="clear" style="background: #fff;position:relative;">
      <div >
        <div class="imgbox">
          <ul>
            <li>
              <img src="../../assets/img/666.jpg" alt="">
              <p>入口对比照片</p>
            </li>
            <li>
              <img src="../../assets/img/666.jpg" alt="">
              <p>出口对比照片</p>
            </li>
          </ul>
        </div>
      </div>
      <div class="list_ul am-u-md-6">
        <ul>
          <li>
            <span class="text">车牌号码：</span>
            <span class="more">
              <div class="el-input">
                <input type="text" value="粤BW4Q53"  class="el-input__inner" />
              </div>
            </span>
          </li>
          <li>
            <span class="text">入场时间：</span>
            <span class="more">
              <div class="el-input">
                <input type="text" value="2017-05-15 12:00:00"  class="el-input__inner" />
              </div>
            </span>
          </li>
          <li>
            <span class="text">停车时长：</span>
            <span class="more">
              <div class="el-input">
                <input type="text" value="1小时20分"  class="el-input__inner" />
              </div>
            </span>
          </li>
          <li>
            <span class="text">车辆类型：</span>
            <span class="more">
              <el-select v-model="select">
                <el-option label="小型车" value="1"></el-option>
                <el-option label="中型车" value="2"></el-option>
                <el-option label="大型车" value="3"></el-option>
              </el-select>
            </span>
          </li>
          <li>
            <span class="text">收费金额：</span>
            <span class="more">
              <div class="el-input ">
                <input type="text" value="￥2.00"  class="el-input__inner yellow" readonly="readonly" />
              </div>
            </span>
          </li>
          
         <transition name="slide-fade">
          <div v-if="show">
            <li>
              <span class="text">证件号码：</span>
              <span class="more">
                <div class="el-input">
                  <input type="text" value="49999999999"  class="el-input__inner" />
                </div>
              </span>
            </li>
            <li>
              <span class="text">证件号码：</span>
              <span class="more">
                <div class="el-input">
                  <input type="text" value="49999999999"  class="el-input__inner" />
                </div>
              </span>
            </li>
          </div>
        </transition>
        </ul>
        <p @click="show = !show" class="list_down"> 展开更多<i class="am-header-icon" v-bind:class="{'am-icon-angle-double-up': show, 'am-icon-angle-double-down':!show }"></i></p>
      </div>

      <div class="fixed_but am-u-md-6">
        <p style="">收费金额：￥2.00</p>
        <ul>
          <li><router-link to="/charge">确认收费</router-link></li>
          <li>免费放行</li>
          <li>取消放行</li>
          <li>停车折扣</li>
        </ul>
      </div>

    </div>
  </template>
  <script>
  export default {
    data () {
      return {
        select: '小型车',
        show: 0
      }
    },
    created () {
      this.$parent.$children[0].headTitle = '收费'
      this.$parent.$children[0].bar_show = 1
    }
  }

  </script>

  <style scoped>

    @media only screen and (min-width:641px) {
      .list_ul {
        margin: 10px 40px !important;
      }
    }
    [class*=am-u-]{
      padding: 0px !important;
    }
    .imgbox {
      width: 100%;
      overflow: hidden;
    }
    .imgbox ul li:nth-of-type(1){
      margin-right: 2%;
    }
    .imgbox ul li{
      width: 49%;
      float: left;
      position: relative;
      list-style: none;
    }
    .imgbox ul li p{
      position: absolute;
      left: 0px;
      bottom: 0px;
      margin: 0px;
      padding: 0px;
      color: #fff;
      text-align: center;
      width: 100%;
      line-height: 24px;
      font-size: 12px;
      height: 24px;
      background: rgba(0,0,0,0.7)
    }
    .imgbox img {
      width: 100%;
    }
    

  </style>



