<template>
  <div class="CentralCharge">
    <div class="warning">
      
    </div>
    <div class="carImg">
      <img src="../../assets/img/666.jpg" alt="">
    </div>
    <el-tabs v-model="activeName" @tab-click="handleClick">

      <div class="search">
        <span>出场车牌：</span>
        <el-input placeholder="请输入内容" icon="search" :on-icon-click="handleIconClick" v-model="input5">
          <el-select v-model="select" slot="prepend" placeholder="粤">
            <el-option label="粤" value="1"></el-option>
          </el-select>
        </el-input>
        
      </div>
      <p class="centralWarning">选择与出场车辆相符的照片出场</p>
      <el-tab-pane label="号牌相似" name="first" class="clear">
        <ul class="carList">
          <li v-for="n in 5" class=" am-u-md-4 ">
            <div class="carcont clear">
              <div class="imgbox">
                <img src="../../assets/img/666.png" alt="">
              </div>
              <div class="text">
                <p>粤B0001</p>
                <span>入场时间：2017-02-15 12:00:00</span>
              </div>
            </div>
          </li>
        </ul>
      </el-tab-pane>
      <el-tab-pane label="无车牌" name="second">
        <ul class="carList">
          <li v-for="n in 2" class=" am-u-md-4 ">
            <div class="carcont clear">
              <div class="imgbox">
                <img src="../../assets/img/666.png" alt="">
              </div>
              <div class="text">
                <p>粤B0001</p>
                <span>入场时间：2017-02-15 12:00:00</span>
              </div>
            </div>
          </li>
        </ul>
      </el-tab-pane>
      <el-tab-pane label="手动入场" name="third">
        <ul class="carList">
          <li v-for="n in 3" class=" am-u-md-4 ">
            <div class="carcont clear">
              <div class="imgbox">
                <img src="../../assets/img/666.png" alt="">
              </div>
              <div class="text">
                <p>粤B0001</p>
                <span>入场时间：2017-02-15 12:00:00</span>
              </div>
            </div>
          </li>
        </ul>
      </el-tab-pane>
      <el-tab-pane label="无入场照片" name="fourth">
        <ul class="carList">
          <li v-for="n in 4" class=" am-u-md-4 ">
            <div class="carcont clear">
              <div class="imgbox">
                <img src="../../assets/img/666.png" alt="">
              </div>
              <div class="text">
                <p>粤B0001</p>
                <span>入场时间：2017-02-15 12:00:00</span>
              </div>
            </div>
          </li>
        </ul>
      </el-tab-pane>
    </el-tabs>

  </div>
</template>
<script>
import Warning from './warning'
export default {
  data () {
    return {
      activeName: 'first',
      input5: '',
      select: '粤'
    }
  },
  methods: {
    handleClick (tab, event) {
      console.log(tab, event)
    },
    handleIconClick (ev) {
      console.log(ev)
    }
  },
  created () {
    this.$parent.$children[0].headTitle = '手动出场'
    this.$parent.$children[0].bar_show = 1
  },
  components: {
    Warning
  }
}

</script>

<style scoped>
.search{
  height: 65px;
  background: #fff;
  padding: 15px 10px;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.search > span{
  float: left;
  line-height: 35px;
}
.el-input-group{
  float: left;
  width: 70%;
  max-width: 400px;
}
.carList li {
  padding: 10px;
  background: #fff;
  border-bottom: 1px solid #ddd;
}
.carList li .imgbox{
  width: 60px;
  height: 60px;
  float: left;
}
.carList li .imgbox img{
  width: 100%;
  height: 100%;
}
.carList li .text{
  padding: 5px 0 0 10px;
  float: left;
}
.carList li .text p{
  margin: 0px;
  color: #000;
  font-size: 16px;
  font-weight: 500;
}
.carList li .text span{
  color: #999;
  font-size: 14px;
}

</style>



